<template>
  <div class="data">
    <el-form class="form" :inline="true">
      <el-form-item :label="msg.date">
        <el-date-picker v-model="date" type="date"> </el-date-picker>
      </el-form-item>

      <el-form-item>
        <el-select class="a" v-model="radio">
          <el-option label="统计" :value="0"> </el-option>
          <el-option label="筛选" :value="1"> </el-option>
          <el-option label="明细" :value="2"> </el-option>
        </el-select>
      </el-form-item>

      <el-form-item class="b">
        <el-button @click="delData" type="danger">{{ msg.btn }}</el-button>
      </el-form-item>
    </el-form>

    <Static_2 v-show="radio === 0" :datas="history.datas" />
    <Filter_2 v-show="radio === 1" :datas="history.datas" />
    <Detail_2 v-show="radio === 2" :details="history.details" />
  </div>
</template>

<script>
  import Static_2 from "@/views/data/Static.vue";
  import Filter_2 from "@/views/data/Filter.vue";
  import Detail_2 from "@/views/data/Detail.vue";
  import { mapState } from "vuex";

  export default {
    name: "Data",
    components: { Static_2, Filter_2, Detail_2 },
    data() {
      return {
        date: new Date(),
        history: {},
        radio: 0,
      };
    },
    computed: {
      ...mapState(["ymd", "infos", "getTime"]),
      msg() {
        return this.isToday(this.date)
          ? { btn: "删除今日数据", date: "今日数据" }
          : { btn: "删除该日数据", date: "历史数据" };
      },
    },
    watch: {
      date: {
        immediate: true,
        handler(newVal, oldVal) {
          console.log(
            "newVal: ",
            this.getTime(newVal),
            "  oldVal: ",
            this.getTime(oldVal)
          );

          if (this.isToday(newVal)) {
            this.history = this.infos;
          } else {
            let { ymd } = this.getTime(newVal);
            let infos = JSON.parse(localStorage.getItem(ymd));

            if (infos === null) {
              this.$notify({
                message: `${ymd} 的数据不存在！`,
                type: "error",
                position: "top-right",
                offset: 50,
                duration: 2000,
              });

              this.date = oldVal;
            } else this.history = infos;
          }
        },
      },
    },
    methods: {
      isToday(date) {
        return this.getTime(date).ymd === this.getTime(new Date()).ymd;
      },

      delData() {
        let { ymd } = this.getTime(this.date);
        this.$confirm(`确定删除 ${ymd} 的数据吗？`, "慎重！", {
          confirmButtonText: "删除",
          cancelButtonText: "算了",
          type: "warning",
        })
          .then(() => {
            if (ymd !== this.ymd) {
              localStorage.removeItem(ymd);
              this.date = new Date();
            } else {
              // 提交
              this.$store.commit("reset");
              this.history = this.infos; // infos => computed
            }
            this.$notify({
              message: `已删除！`,
              type: "success",
              position: "top-right",
              offset: 50,
              duration: 2000,
            });
          })
          .catch(() => {
            this.$notify({
              message: `已取消删除！`,
              type: "info",
              position: "top-right",
              offset: 50,
              duration: 2000,
            });
          });
      },
    },
  };
</script>

<style lang="css" scoped>
  .data {
    box-sizing: border-box;
  }
  

  .data .form {
    text-align: center;
    margin: 0 auto;
    padding: 5px 0;
    border-bottom: 1px solid #e4e1e1;
  }

  .data .form .el-form-item {
    margin: 0;
  }
  .data .form .a {
    margin: 0 70px;
  }
  .b {
    padding-left: 70px;
    border-left: 1px solid rgba(167, 173, 183, 0.5);
  }
</style>
