<template>
  <div class="detail">
    <div ref="list" class="list">
      <el-empty
        :image-size="250"
        description="空空的，什么都没有下注！"
        v-if="detailData.length === 0"
      ></el-empty>
      <el-row
        v-for="(d, index) in detailData"
        :key="index"
        :style="{ backgroundColor: d.bg ?? '#fff' }"
      >
        <el-col :span="2">{{
          index + 1 > 9 ? index + 1 : "0" + (index + 1)
        }}</el-col>
        <el-col :span="8">{{ d.time }}</el-col>
        <el-col class="a" :span="6">{{ d.haoMa }}</el-col>

        <el-col class="b" :span="5"
          ><span class="c">{{ d.money }}</span> <span> | </span>
          {{ d.item.length * d.money }}￥</el-col
        >
        <el-col :span="3"
          ><el-button
            v-if="d.money > 0"
            class="delBtn"
            size="mini"
            type="danger"
            icon="el-icon-delete"
            circle
            @click="deleteHao(index)"
          ></el-button>

          <p v-if="d.money <= 0">已退</p>
        </el-col>
      </el-row>
    </div>
    <div></div>
    <div></div>
  </div>
</template>

<script>
  import { mapGetters, mapState } from "vuex";
  export default {
    name: "Detail",
    data() {
      return {};
    },
    computed: {
      ...mapGetters({ detailData: "details" }),
      ...mapState(["getTime"]),
    },
    props: ["scroll"],
    watch: {
      scroll: {
        // immediate: true,
        handler() {
          this.$nextTick(() => {
            this.$refs.list.scrollTo({
              top: this.$refs.list.scrollHeight,
              behavior: "smooth",
            });
          });
        },
      },
    },
    // activated() {
    //   this.$nextTick(() => {
    //     this.$refs.list.scrollTo({
    //       top: this.$refs.list.scrollHeight,
    //       behavior: "smooth",
    //     });
    //   });
    // },

    methods: {
      // 退码
      deleteHao(index) {
        this.$confirm(
          `确定退掉 ${this.detailData[index].haoMa} = ${this.detailData[index].money} 吗?`,
          "",
          {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
          }
        )
          .then(() => {
            const data = this.detailData[index];
            data.money = -data.money;

            console.log(data);

            // 发数据给Vuex
            this.$store.commit("update", { data, index });

            this.$message({
              message: `退码成功！`,
              type: "success",
              offset: 100,
              duration: 3000,
            });
          })
          .catch(() => {
            this.$message({
              message: `取消退码！`,
              type: "info",
              offset: 100,
              duration: 3000,
            });
          });
      },
    },
  };
</script>

<style lang="css" scoped>
  .detail {
    font-family: "Times New Roman";
    font-size: 18px;
  }

  .detail .list {
    /* background-color: #1c1a1a; */
    height: 485px;
    overflow: auto;
    padding: 10px 20px;
  }

  .detail .list .el-row {
    line-height: 30px;
    border-bottom: solid 1px rgb(216, 219, 217);
    padding: 5px 0px;
    text-align: center;
  }

  .detail .list .el-row .hao {
    text-align: left;
  }

  .a {
    font-family: "微软雅黑";
  }
  .a,
  .b {
    /* font-family: ''; */
    text-align: left;
  }

  .b {
    color: rgb(5, 174, 5);
  }

  .b .c {
    border: 1px solid;
    padding: 3px;
    text-align: center;
    border-radius: 50%;
    font-size: 16px;
    font-weight: 600;
  }
</style>
