<template>
  <!-- 下注详情 -->
  <div class="quikhit">
    <Detail :scroll="scroll" />

    <!-- 快打 -->
    <el-form :inline="true" class="form">
      <el-form-item>
        <!-- <el-popover
          placement="left"
          width="200"
          trigger="hover"
          v-model="popover_visible"
        >
          <div class="keyboard">
            <span @click="push('X')">X</span
            ><span @click="push('XX')">XX</span>
            <span v-for="i in 10" :key="i" @click="push(i - 1)">{{
              i - 1
            }}</span
            ><span class="btn" @click="del(false)">C</span
            ><span class="btn" @click="del(true)">CA</span>
          </div> -->
        <el-popover
          placement="left"
          width="300"
          trigger="hover"
          content='格式: X8X8, XX88。" + " ➡️ XX，" - " 或任意英文字母 ➡️ X '
        >
          <el-input
            :maxlength="4"
            slot="reference"
            v-model.trim="haoMa"
            ref="ipt1"
            placeholder="快打"
            clearable
            @input="setValue(0)"
            @keyup.enter.native="$refs.ipt2.focus()"
          ></el-input>
        </el-popover>
      </el-form-item>

      <el-form-item>
        <el-popover content="输入金额，按回车下注，下同" trigger="hover" placement="top">
          <el-input-number
          slot="reference"
            controls-position="right"
            v-model="money"
            placeholder="金额"
            ref="ipt2"
            :min="0"
            @focus="$refs.ipt2.select()"
            @keyup.enter.native="process(0)"
          ></el-input-number
        ></el-popover>
      </el-form-item>

      <el-form-item>
        <el-button
          icon="el-icon-shopping-cart-1"
          @click="process(0)"
          type="primary"
          >下注</el-button
        >
      </el-form-item>
    </el-form>

    <!-- 快选 -->
    <el-form :inline="true" class="form">
      <el-form-item>
        <!-- <el-popover
          placement="left"
          width="200"
          trigger="hover"
          v-model="popover_visible_2"
        >
          <div class="keyboard">
            <span @click="push_2('X')">X</span
            ><span @click="push_2('XX')">XX</span>
            <span @click="push_2(':')">:</span>
            <span v-for="i in 10" :key="i" @click="push_2(i - 1)">{{
              i - 1
            }}</span
            ><span class="btn" @click="del_2(false)">C</span
            ><span class="btn" @click="del_2(true)">CA</span>
            <span @click="ok">OK</span>
          </div> -->
        <el-popover
          placement="left"
          width="300"
          trigger="hover"
          content='格式: X87X87, XX87:87。空格、句号(. 。)、分号(; ；)、冒号(：) ➡️ " : " ，输完按回车'
        >
          <el-input
            slot="reference"
            type="text"
            v-model="haoMa_2"
            @input="setValue(1)"
            clearable
            ref="ipt3"
            @keyup.enter.native="$refs.ipt4.focus()"
            placeholder="快选"
          ></el-input>
        </el-popover>
      </el-form-item>

      <el-form-item>
        <el-input-number
          controls-position="right"
          v-model="money_2"
          placeholder="金额"
          ref="ipt4"
          :min="0"
          @focus="$refs.ipt4.select()"
          @keyup.enter.native="process(1)"
        ></el-input-number>
      </el-form-item>

      <el-form-item>
        <el-button
          icon="el-icon-shopping-cart-2"
          @click="process(1)"
          type="primary"
          >下注</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import { mapState } from "vuex";
  import Detail from "@/components/Detail.vue";
  export default {
    name: "QuickHit",
    components: { Detail },
    data() {
      return {
        // 快打
        haoMa: "",
        money: 0,
        type: new Map([
          ["12", 0],
          ["13", 1],
          ["23", 2],
          ["03", 3],
          ["01", 4],
          ["02", 5],
        ]),
        reg: /^[0-9]{1}X{2}[0-9]{1}$|^X[0-9]{1}X[0-9]{1}$|^[0-9]{1}X[0-9]{1}X$|^X{2}[0-9]{2}$|^[0-9]{2}X{2}$|^X[0-9]{2}X$/,

        // 快选
        haoMa_2: "",
        money_2: 0,
        reg_2:
          /^[0-9]{1,}XX[0-9]{1,}$|^X[0-9]{1,}X[0-9]{1,}$|^[0-9]{1,}X[0-9]{1,}X$|^XX[0-9]{1,}:[0-9]{1,}$|^[0-9]{1,}:[0-9]{1,}XX$|^X[0-9]{1,}:[0-9]{1,}X$/,

        type_2: new Map([
          ["11", 0],
          ["22", 1],
          ["23", 2],
          ["03", 3],
          ["01", 4],
          ["00", 5],
        ]),
        details: [],

        popover_visible: false,
        popover_visible_2: false,
        scroll: true,
      };
    },

    computed: {
      ...mapState(["ymd", "getTime"]),
    },
    activated() {
      this.$refs.ipt1.focus();
    },

    methods: {
      // push(value) {
      //   if (this.haoMa.length >= 4) return;

      //   this.haoMa += value;
      //   // this.$refs.ipt1.focus();
      //   this.setValue(0);
      // },
      // del(delAll) {
      //   if (!delAll) {
      //     this.haoMa = this.haoMa.slice(0, this.haoMa.length - 1);
      //     return;
      //   }
      //   this.haoMa = "";
      // },

      // push_2(value) {
      //   this.haoMa_2 += value;
      // },
      // del_2(delAll) {
      //   if (!delAll) {
      //     this.haoMa_2 = this.haoMa_2.slice(0, this.haoMa_2.length - 1);
      //     return;
      //   }
      //   this.haoMa_2 = "";
      // },
      // ok() {
      //   this.$refs.ipt4.focus();
      //   this.popover_visible_2 = false;
      // },

      setValue(key) {
        //实时修改快打输入框内容
        if (!key) {
          let str = this.haoMa;
          /\-|[A-z]/.test(str) && (this.haoMa = str.replace(/\-|[A-z]/g, "X"));
          /\+/.test(str) && (this.haoMa = str.replace(/\+/g, "XX"));

          if (this.reg.test(this.haoMa)) {
            this.$refs.ipt2.focus();
            this.popover_visible = false;
          }
        } else {
          //实时修改快选输入框内容
          let str = this.haoMa_2;

          /\-|[A-z]/.test(str) &&
            (this.haoMa_2 = str.replace(/\-|[A-z]/g, "X")); // - 和 任意字母 => X

          /\+/.test(str) && (this.haoMa_2 = str.replace(/\+/g, "XX")); // + => XX

          /\s|\.|\。|\;|\：|\；/.test(str) &&
            (this.haoMa_2 = str.replace(/\s|\.|\。|\;|\：|\；/g, ":")); // 空格，. => :
        }
      },
      err(err, offset) {
        this.$notify({
          // message: err,
          title: `${err}`,
          type: "error",
          position: "bottom-right",
          offset: offset,
          duration: 3000,
        });
      },

      /** 处理快打数据 */
      process(key) {
        // 把数据处理成这样
        /*
            data = {
              type: 0,
              time:'',
              haoMa:'',
              money: 10,
              item: [
                { first: 1, last: 6 },
                { first: 1, last: 5 },
              ],
            };
        */

        const data = {
          type: 0,
          time: "",
          haoMa: "",
          money: 0,
          item: [],
          bg: "#fff",
        };

        // 快打
        if (!key) {
          let haoMa = this.haoMa,
            money = this.money;

          // 0.判断
          if (!this.reg.test(haoMa)) {
            this.err("号码输入错误了哦！", 60);
            this.$refs.ipt1.focus();
            return;
          } else if (!money) {
            this.err("金额输入错误了哦！", 60);
            this.money = 0;
            return;
          }

          // 1.根据X的位置判断号码类型
          data.type = this.type.get(
            haoMa.indexOf("X") + "" + haoMa.lastIndexOf("X")
          );

          // 2.下注时间
          let { hms } = this.getTime(new Date());
          data.time = this.ymd + " " + hms;

          // 3.下注号码
          data.haoMa = haoMa;

          // 4.下注金额
          data.money = money;

          // 5.号码拆解
          const arr = haoMa
            .split("") // 1XX8 => ['1','','8']
            .filter((value) => value != "X"); // ['1','8']
          const item = {
            first: parseInt(arr[0]),
            last: parseInt(arr[1]),
          };

          // 将数据压进数组
          data.item.push(item);

          console.log(data);
        }

        //快选
        else {
          let haoMa = this.haoMa_2,
            money = this.money_2;

          // 0.错误判断
          if (!this.reg_2.test(haoMa)) {
            this.err("号码输入错误了哦！", 0);
            this.$refs.ipt3.focus();
            return;
          } else if (!money) {
            this.err("金额输入错误了哦！", 0);
            this.money = 0;
            return;
          }

          const hao = haoMa.split(/X|:/gi);
          data.type = this.type_2.get(
            hao.indexOf("") + "" + hao.lastIndexOf("")
          );

          let { hms } = this.getTime(new Date());
          data.time = this.ymd + " " + hms;

          data.haoMa = haoMa.replace(/:/g, " : ");

          // 4.下注金额
          data.money = this.money_2;

          // 5.号码拆解
          const arr = hao.filter((value) => value != "");

          // 号码  分解  去重
          const arr_1 = Array.from(new Set(arr[0].split("")));

          // 转换成整数
          arr_1.forEach((value, index, arr) => {
            arr[index] = parseInt(value);
          });

          // 号码  分解  去重
          const arr_2 = Array.from(new Set(arr[1].split("")));
          // 转换成整数
          arr_2.forEach((value, index, arr) => {
            arr[index] = parseInt(value);
          });

          console.log(arr_1, arr_2);

          for (let i = 0; i < arr_1.length; i++) {
            for (let j = 0; j < arr_2.length; j++) {
              const item = {
                first: arr_1[i],
                last: arr_2[j],
              };
              data.item.push(item);
            }
          }
          console.log(data);
        }

        // 发数据给Vuex
        this.$store.commit("update", { data });

        this.scroll = !this.scroll;

        // 清空输入框
        if (!key) {
          this.haoMa = "";
          this.$refs.ipt1.focus();
        } else {
          this.haoMa_2 = "";
          this.$refs.ipt3.focus();
        }

        this.$notify({
          // message: `成功下注 ${data.haoMa} = ${data.money}`,
          type: "success",
          title: "下注成功！",
          position: "bottom-right",
          offset: 60,
          duration: 3000,
        });
      },
    },
  };
</script>

<style lang="css" scoped>
  .quikhit .form {
    text-align: center;
    padding: 10px;
  }
  .quikhit .form .el-form-item {
    margin: 0 10px;
  }

  .keyboard span {
    width: 40px;
    text-align: center;
    display: inline-block;

    font-size: 20px;
    margin: 5px;

    box-shadow: 0px 0px 3px 0px #ccc;
    border-radius: 3px;
    cursor: pointer;
    user-select: none;
    transition: background 0.05s;
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #00a67c,
      0 0 70px #00a67c, 0 0 80px #00a67c, 0 0 100px #00a67c, 0 0 150px #00a67c;
  }

  .keyboard .btn {
    background-color: #f56c6c;
  }

  .keyboard span:hover {
    background: #eee;
  }
</style>
