<template>
  <div class="test">
   
    {{ obj }}
    {{ num[0] }}---{{ num[1] }}

    <br />
    {{ sum[0] }}---{{ sum[1] }} <br />
    <br />
    {{ list[0].name }}---{{ list[1].name }}
    <br />
    <button @click="change()">change</button>

    <button @click="change2()">change2</button>

    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="审批人">
        <el-input placeholder="审批人"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary">查询</el-button>
      </el-form-item>
    </el-form>

    输入号码：<el-input
      type="text"
      v-model="haoMa"
      clearable
      @input="setValue"
      placeholder="输入单组号码，如X8X8"
    ></el-input>
    号码按' : '拆解：{{ haoMa.split(/:/g) }}<br />

    号码按'X'拆解：{{ haoMa.split(/X/gi) }}<br />
    号码按':或X'拆解：{{ haoMa.split(/X|:/gi) }}

    <br />
    <br />
    <br />
    <el-button @click="d">重置数据</el-button>

    <br />
    <el-date-picker v-model="date" type="date"> </el-date-picker>
    日期：{{ date instanceof Date }} <br />
    Date:{{ getTime(date) }}

    <br />
    map:{{ map }}
    <br />

    <el-button @click="test">ddd</el-button>

    <br />

    <br />

    <div class="block">
      <span class="demonstration">默认 click 触发子菜单</span>
      <el-cascader
        v-model="value"
        :options="options"
        @change="handleChange"
      ></el-cascader>
      value: {{ value }}
    </div>
    <div class="block">
      <span class="demonstration">hover 触发子菜单</span>
      <el-cascader
        v-model="value"
        :options="options"
        :props="{ expandTrigger: 'hover' }"
        @change="handleChange"
      ></el-cascader>
      value: {{ value }}
    </div>

    <br />
    <br />
    {{ [8].indexOf("8") }}

    <br />
    <br />
    <el-button @click="print">print pl5.json</el-button>
  </div>
</template>

<script>
 
  import axios from "axios";

  export default {
    name: "Test",
    data() {
      return {
        obj: [],
        num: [0, 4],
        sum: [0, 4],
        list: [{ name: "小猪" }, { name: "xxx" }],
        haoMa: "XX67:80",
        date: new Date(),

        value: [],
        options: [
          {
            // value: 'zhinan',
            label: "指南",
            children: [
              {
                value: "shejiyuanze",
                label: "设计原则",
                children: [
                  {
                    value: "yizhi",
                    label: "一致",
                  },
                ],
              },
              {
                value: "daohang",
                label: "导航",
                children: [
                  {
                    value: "cexiangdaohang",
                    label: "侧向导航",
                  },
                  {
                    value: "dingbudaohang",
                    label: "顶部导航",
                  },
                ],
              },
            ],
          },
          {
            value: "zujian",
            label: "组件",
            children: [
              {
                value: "basic",
                label: "Basic",
                children: [
                  {
                    value: "layout",
                    label: "Layout 布局",
                  },
                  {
                    value: "color",
                    label: "Color 色彩",
                  },
                  {
                    value: "typography",
                    label: "Typography 字体",
                  },
                  {
                    value: "icon",
                    label: "Icon 图标",
                  },
                  {
                    value: "button",
                    label: "Button 按钮",
                  },
                ],
              },
              {
                value: "form",
                label: "Form",
                children: [
                  {
                    value: "radio",
                    label: "Radio 单选框",
                  },
                  {
                    value: "checkbox",
                    label: "Checkbox 多选框",
                  },
                ],
              },
              {
                value: "data",
                label: "Data",
                children: [
                  {
                    value: "table",
                    label: "Table 表格",
                  },
                  {
                    value: "tag",
                    label: "Tag 标签",
                  },
                ],
              },
              {
                value: "notice",
                label: "Notice",
                children: [
                  {
                    value: "alert",
                    label: "Alert 警告",
                  },
                  {
                    value: "loading",
                    label: "Loading 加载",
                  },
                ],
              },
              {
                value: "navigation",
                label: "Navigation",
                children: [
                  {
                    value: "breadcrumb",
                    label: "Breadcrumb 面包屑",
                  },
                  {
                    value: "dropdown",
                    label: "Dropdown 下拉菜单",
                  },
                ],
              },
              {
                value: "others",
                label: "Others",
                children: [
                  {
                    value: "dialog",
                    label: "Dialog 对话框",
                  },
                  {
                    value: "tooltip",
                    label: "Tooltip 文字提示",
                  },
                ],
              },
            ],
          },
          {
            value: "ziyuan",
            label: "资源",
            children: [
              {
                value: "axure",
                label: "Axure Components",
              },
            ],
          },
        ],
      };
    },
    // activated() {
    //   axios.get(`/json/pl5-all.json`).then(
    //     (res) => {
    //       let data = res.data;
    //       calc(data);
    //     },
    //     (err) => {
    //       console.error(err);
    //     }
    //   );
    // },
    computed: {
      map() {
        let arr = [9, "", 9];
        return arr.indexOf("") + "" + arr.lastIndexOf("");
      },
    },

    methods: {
      print() {
        this.$axios
          .get("")
          .then(function (response) {
            console.log(response.data);
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      handleChange(value) {
        console.log(value);
      },
      //实时修改input值
      //实时修改输入框内容
      setValue() {
        let str = this.haoMa;

        // - ，任意字母 => X
        /\-|[A-z]/g.test(str) && (this.haoMa = str.replace(/\-|[A-z]/g, "X"));

        // + => XX
        /\+/g.test(str) && (this.haoMa = str.replace(/\+/g, "XX"));

        // 空格，/ => :
        /\s|\/|\./g.test(str) && (this.haoMa = str.replace(/\s|\/|\./g, ":"));
      },

      change() {
        this.obj.push(0);
        this.num[0] = 9;
        this.sum[0] = 8;
        this.list[0].name = "ooo";
        console.log(this.obj, this.num, this.sum);
      },
      change2() {
        this.obj.push(0);
        let num = this.num;
        num[0] = 1000;
      },

      d() {
        console.log("组件被创建了");
      },
      getTime(date) {
        let month = zeroFill(date.getMonth() + 1); //月
        let day = zeroFill(date.getDate()); //日
        let hour = zeroFill(date.getHours()); //时
        let minute = zeroFill(date.getMinutes()); //分
        let second = zeroFill(date.getSeconds()); //秒

        //年月日
        let hms = hour + ":" + minute + ":" + second;

        let ymd = date.getFullYear() + "-" + month + "-" + day;

        return { ymd: ymd, hms: hms };

        /**
         * 补零
         */
        function zeroFill(time) {
          if (time >= 0 && time <= 9) {
            return "0" + time;
          } else {
            return time;
          }
        }
      },

      test() {
        let obj = {
          "0-6": '我是obj对象的属性"0-6"',
          "3-8"() {
            console.log("我是方法'3-8', ", this["0-6"]);
          },
        };

        obj["3-8"]();
      },

      time() {
        let arr = [];
        for (let i = 0; i < 100000; i++) {
          arr.push(i);
        }

        let start = performance.now(); // 计算耗时
        for (let i = 0; i < arr.length; i++) {
          // arr[i]=0
        }
        //计算耗时
        let end = performance.now();
        console.log("计算时间1：", `${end - start}ms`);

        let start2 = performance.now(); // 计算耗时

        for (let i = 0, l = arr.length; i < l; i++) {
          // arr[i]=0
        }
        //计算耗时
        let end2 = performance.now();
        console.log("计算时间2：", `${end2 - start2}ms`);

        let start3 = performance.now(); // 计算耗时

        for (let x of arr) {
          // arr[i]=0
        }
        //计算耗时
        let end3 = performance.now();
        console.log("计算时间3：", `${end3 - start3}ms`);

         let start4 = performance.now(); // 计算耗时
        
        arr.forEach(()=>{})
        //计算耗时
        let end4 = performance.now();
        console.log("计算时间4：", `${end4 - start4}ms`);
      },
    },
    mounted() {
      let str = "1XX6";
      let arr = str.split(/[A-z]/);
      console.log(arr, parseInt(str), "1" > 1);

      let arr2 = [2, 3, 5, 6, 78, 8];
      let arr3 = arr2.filter((num) => num > 5);
      console.log(arr3);

      let reg =
        //       1XX8              X1X8                     1X8X                xx18                 18xx              X18X
        /^[0-9]{1}XX[0-9]{1}$|^X[0-9]{1}X[0-9]{1}$|^[0-9]{1}X[0-9]{1}X$|^XX[0-9]{1}[0-9]{1}$|^[0-9]{1}[0-9]{1}XX$|^X[0-9]{1}[0-9]{1}X$/i;
      console.log("reg1匹配：", reg.test("xx16 "));

      let reg2 = /^[0-9]+$|^(\s){0}$/g;

      console.log(reg2.test("786"));

      this.time();


      
    },
  };
</script>

<style scoped>
  

  .demo-form-inline {
    background-color: #bfc;
  }

  
</style>
