//引入VueRouter
import VueRouter from "vue-router";
//引入组件
import ComNum from "@/views/ComNum";
import QuickHit from "@/views/QuickHit";
import Index from "@/views/Index";
import Tip from "@/views/other/Tip";
// import System from "@/views/other/System";
import Test from "@/views/other/Test";
import Data from "@/views/data/Data";
// import Analysis from "@/views/Analysis";
// import Static from '@/views/static/Static'
// import Calculate from '@/views/Calculate'

//创建router实例对象，去管理一组一组的路由规则
const router = new VueRouter({
  mode: "hash",
  routes: [
    {
      path: "/comnum",
      component: ComNum,
    },
    {
      path: "/quickhit",
      component: QuickHit,
    },
    {
      path: "/",
      component: Index,
    },
    {
      path: "/tip",
      component: Tip,
    },
    
    {
      path: "/test",
      component: Test,
    },
    {
      path: "/data",
      component: Data,
    }
  ],
});

//暴露router
export default router;
