<template>
  <!-- 下注详情 -->
  <div class="comnum">
    <Detail :scroll="scroll" />

    <!-- 合数 -->
    <el-form :inline="true" class="form">
      <el-form-item>
        <el-popover
          content="选择2个位置，并输入合数"
          trigger="hover"
          width="170"
          placement="left"
        >
          <el-checkbox-group
            slot="reference"
            @change="focus(0)"
            v-model="checkedList"
            :max="2"
          >
            <el-checkbox-button label="千">千</el-checkbox-button>
            <el-checkbox-button label="百">百</el-checkbox-button>
            <el-checkbox-button label="十">十</el-checkbox-button>
            <el-checkbox-button label="个">个</el-checkbox-button>
          </el-checkbox-group>
        </el-popover>
      </el-form-item>
      <el-form-item>
        <el-input
          v-model.trim="heNum"
          ref="ipt1"
          placeholder="合数"
          clearable
          @keyup.enter.native="$refs.ipt2.focus()"
        ></el-input>
      </el-form-item>

      <el-form-item>
        <el-tooltip content="输入金额，回车下注，下同" placement="top">
          <el-input-number
            controls-position="right"
            v-model="money"
            placeholder="金额"
            ref="ipt2"
            :min="0"
            @focus="$refs.ipt2.select()"
            @keyup.enter.native="process(0)"
          ></el-input-number>
        </el-tooltip>
      </el-form-item>

      <el-form-item>
        <el-button icon="el-icon-sold-out" @click="process(0)" type="primary"
          >下注</el-button
        >
      </el-form-item>
    </el-form>

    <!-- 全数 -->
    <el-form :inline="true" class="form">
      <el-form-item>
        <el-popover
          content="选择1个位置，并输入该位置号码"
          trigger="hover"
          width="170"
          placement="left"
        >
          <el-radio-group @input="focus(1)" slot="reference" v-model="radio">
            <el-radio-button label="千">千</el-radio-button>
            <el-radio-button label="百">百</el-radio-button>
            <el-radio-button label="十">十</el-radio-button>
            <el-radio-button label="个">个</el-radio-button>
          </el-radio-group>
        </el-popover>
      </el-form-item>
      <el-form-item>
        <el-popover placement="top" trigger="hover" width="200">
          <div>输入 {{ radio }}位 号码，数字可重复，生成号码：{{ tip2 }}</div>
          <el-input
            slot="reference"
            v-model.trim="quanNum"
            ref="ipt3"
            placeholder="全数"
            clearable
            @keyup.enter.native="$refs.ipt4.focus()"
          ></el-input
        ></el-popover>
      </el-form-item>

      <el-form-item>
        <el-input-number
          controls-position="right"
          v-model="money_2"
          placeholder="金额"
          ref="ipt4"
          :min="0"
          @focus="$refs.ipt4.select()"
          @keyup.enter.native="process(1)"
        ></el-input-number>
      </el-form-item>

      <el-form-item>
        <el-button icon="el-icon-sell" @click="process(1)" type="primary"
          >下注</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import { mapState } from "vuex";
  import Detail from "@/components/Detail.vue";
  export default {
    name: "ComNum",
    components: { Detail },
    data() {
      return {
        checkedList: [],
        // 合数
        heNum: "",
        money: 0,
        type: new Map([
          ["千个", 0],
          ["千十", 1],
          ["千百", 2],
          ["百十", 3],
          ["十个", 4],
          ["百个", 5],
        ]),

        // 全数
        radio: "",
        quanNum: "",
        money_2: 0,
        reg: /^[0-9]{1,}$/,

        scroll:true
      };
    },

    computed: {
      ...mapState(["ymd", "getTime"]),

      tip() {
        if (this.checkedList.length !== 2) {
          return "";
        }
        let l = this.checkedList[0];
        let r = this.checkedList[1];
        const wei = ["千", "百", "十", "个"];
        let w = l + r;
        if (wei.indexOf(l) > wei.indexOf(r)) w = r + l;
        if (w === "千个") w = "头尾";
        if (w == "百十") w = "中肚";
        return w;
      },

      tip2() {
        let hao = `${this.quanNum} X X 0~9`;
        this.radio === "百" && (hao = `X ${this.quanNum} X 0~9`);
        this.radio === "十" && (hao = `X X ${this.quanNum} : 0~9`);
        this.radio === "个" && (hao = ` 0~9 X X ${this.quanNum} `);
        return hao;
      },
    },

    methods: {
      focus(key) {
        if (!key) {
          if (this.checkedList.length === 2) {
            this.$refs.ipt1.focus();
          }
        } else {
          this.$refs.ipt3.focus();
        }
      },

      err(err, offset) {
        this.$notify({
          title: `${err}`,

          type: "error",
          position: "bottom-right",
          offset: offset,
          duration: 3000,
        });
      },

      /** 处理数据 */
      process(key) {
        // 把数据处理成这样
        /*
            data = {
              type: 0,
              time:'',
              haoMa:'',
              money: 10,
              item: [
                { first: 1, last: 6 },
                { first: 1, last: 5 },
              ],
            };
            */

        const data = {
          type: 0,
          time: "",
          haoMa: "",
          money: 0,
          item: [],
        };

        // 合数
        if (!key) {
          let heNum = this.heNum;
          let money = this.money;

          // 0.判断
          if (this.checkedList.length !== 2) {
            this.err("位置选择错误了哦！", 60);
            return;
          } else if (!this.reg.test(heNum)) {
            this.err("合数输入错误了哦！", 60);
            this.$refs.ipt1.focus();
            return;
          } else if (!money) {
            this.err("金额输入错误了哦！", 60);
            this.money = 0;
            return;
          }

          // 1.根据X的位置判断号码类型
          let l = this.checkedList[0];
          let r = this.checkedList[1];
          const wei = ["千", "百", "十", "个"];
          let w = l + r;
          if (wei.indexOf(l) > wei.indexOf(r)) w = r + l;
          data.type = this.type.get(w);

          console.log(data.type);

          // 2.下注时间
          let { hms } = this.getTime(new Date());
          data.time = this.ymd + " " + hms;

          // 3.下注号码
          data.haoMa = w + "合" + heNum;

          if (data.type === 0) data.haoMa = "头尾合" + heNum;
          if (data.type === 3) data.haoMa = "中肚合" + heNum;

          // 4.下注金额
          data.money = money;

          console.log(data);

          // 5.号码拆解
          // 号码  分解  去重
          const arr = Array.from(new Set(heNum.split("")));
          // 转换成整数
          arr.forEach((value, index, arr) => {
            arr[index] = parseInt(value);
          });

          arr.forEach((value) => {
            for (let i = 0; i < 10; i++) {
              for (let j = 0; j < 10; j++) {
                if ((i + j) % 10 === value) {
                  data.item.push({ first: i, last: j });
                }
              }
            }
          });

          console.log(data);
        }
        // 全数
        else {
          let quanNum = this.quanNum;
          let money = this.money_2;
          // 0.判断
          if (!this.radio) {
            this.err("位置输入错误了哦！", 0);
            return;
          } else if (!this.reg.test(quanNum)) {
            this.err("号码输入错误了哦！", 0);
            this.$refs.ipt3.focus();
            return;
          } else if (!money) {
            this.err("金额输入错误了哦！", 0);
            this.money_2 = 0;
            return;
          }

          // 1.根据X的位置判断号码类型
          let l = this.radio;
          let w = l + "个";

          if (l === "个") w = "千" + l;

          data.type = this.type.get(w);

          console.log(data.type);

          // 2.下注时间
          let { hms } = this.getTime(new Date());
          data.time = this.ymd + " " + hms;

          // 3.下注号码
          l === "千" && (data.haoMa = `${quanNum}XX全`);
          l === "百" && (data.haoMa = `X${quanNum}X全`);
          l === "十" && (data.haoMa = `XX${quanNum}全`);
          l === "个" && (data.haoMa = `全XX${quanNum}`);

          // 4.下注金额
          data.money = money;

          console.log(data);

          // 5.号码拆解
          // 号码  分解  去重
          const arr = Array.from(new Set(quanNum.split("")));
          // 转换成整数
          arr.forEach((value, index, arr) => {
            arr[index] = parseInt(value);
          });

          if (l !== "个") {
            arr.forEach((value) => {
              for (let i = 0; i < 10; i++) {
                data.item.push({ first: value, last: i });
              }
            });
          } else {
            arr.forEach((value) => {
              for (let i = 0; i < 10; i++) {
                data.item.push({ first: i, last: value });
              }
            });
          }

          console.log(data);
        }

        // 发数据给Vuex.Store
        this.$store.commit("update", { data });

        this.scroll = !this.scroll

        this.$notify({
          message: `成功下注 ${data.haoMa} = ${data.money}`,
          type: "success",
          position: "bottom-right",
          offset: 60,
          duration: 3000,
        });

        if (!key) {
          this.heNum = "";
          this.checkedList = [];
        } else {
          this.quanNum = "";
          this.radio = "";
        }
      },
    },
  };
</script>

<style lang="css" scoped>
  .comnum .form {
    /* background-color: #ee2c2c; */
    text-align: center;
    padding: 10px;
  }

  .comnum .form .el-form-item {
    margin: 0 10px;
  }
</style>
